'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useRouter } from 'next/navigation';
import { 
  Menu, 
  X, 
  User, 
  LogOut, 
  PenTool, 
  Users,
  Heart,
  Sun,
  Moon,
  Globe,
  ChevronDown
} from 'lucide-react';

interface User {
  _id: string;
  name: string;
  email: string;
  role: string;
  status: string;
  avatar?: string;
}

interface NavbarProps {
  user?: User | null;
  onLogout?: () => void;
}

export default function Navbar({ user, onLogout }: NavbarProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [showProfileMenu, setShowProfileMenu] = useState(false);
  const [showThemeMenu, setShowThemeMenu] = useState(false);
  const [showLanguageMenu, setShowLanguageMenu] = useState(false);
  const [currentTheme, setCurrentTheme] = useState('light');
  const [currentLanguage, setCurrentLanguage] = useState('hi');
  const router = useRouter();

  useEffect(() => {
    const handleClickOutside = () => {
      setShowProfileMenu(false);
      setShowThemeMenu(false);
      setShowLanguageMenu(false);
    };

    document.addEventListener('click', handleClickOutside);
    return () => document.removeEventListener('click', handleClickOutside);
  }, []);

  const handleLogout = () => {
    if (onLogout) {
      onLogout();
    }
    localStorage.removeItem('accessToken');
    document.cookie = 'refreshToken=; path=/; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
    router.push('/');
  };

  return (
    <nav className="bg-white shadow-lg border-b border-gray-200 sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo Section */}
          <div className="flex items-center relative">
            <div 
              className="relative group"
              onClick={(e) => e.stopPropagation()}
            >
              <Link href="/" className="flex items-center space-x-3">
                <div className="w-10 h-10 bg-gradient-to-r from-orange-500 to-orange-600 rounded-xl flex items-center justify-center shadow-lg">
                  <span className="text-xl text-white font-bold">üïâÔ∏è</span>
                </div>
                <div className="flex flex-col">
                  <span className="text-2xl font-bold text-orange-600">
                    ‡§∏‡§®‡§æ‡§§‡§®
                  </span>
                  <span className="text-sm text-gray-600 -mt-1">
                    Blogs
                  </span>
                </div>
              </Link>


            </div>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-8">
            <Link
              href="/"
              className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-sm font-medium transition-colors"
            >
              Home
            </Link>
            <Link
              href="/blogs"
              className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-sm font-medium transition-colors"
            >
              Blogs
            </Link>
            <Link
              href="/about"
              className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-sm font-medium transition-colors"
            >
              About Us
            </Link>

            {/* Write Blog - Only show for authenticated users */}
            {user && (
              <Link
                href="/write-blog"
                className="bg-orange-600 text-white hover:bg-orange-700 px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
              >
                <PenTool className="h-4 w-4" />
                <span>Write Blog</span>
              </Link>
            )}

            {/* Action Buttons */}
            <div className="flex items-center space-x-2">
              {/* Donate Button */}
              <Link
                href="/donate"
                className="bg-gradient-to-r from-pink-500 to-red-500 text-white hover:from-pink-600 hover:to-red-600 px-4 py-2 rounded-xl text-sm font-medium transition-all duration-300 flex items-center space-x-2 shadow-lg hover:shadow-xl transform hover:scale-105"
              >
                <Heart className="h-4 w-4" />
                <span>Donate</span>
              </Link>

              {/* Theme Toggle */}
              <div className="relative" onClick={(e) => e.stopPropagation()}>
                <button
                  onClick={() => setShowThemeMenu(!showThemeMenu)}
                  className="bg-gray-100 hover:bg-gray-200 text-gray-700 px-3 py-2 rounded-xl text-sm font-medium transition-all duration-300 flex items-center space-x-1"
                >
                  {currentTheme === 'light' ? (
                    <Sun className="h-4 w-4" />
                  ) : (
                    <Moon className="h-4 w-4" />
                  )}
                  <ChevronDown className="h-3 w-3" />
                </button>

                {showThemeMenu && (
                  <div className="absolute right-0 mt-2 w-36 bg-white rounded-xl shadow-lg border border-gray-200 py-2 z-50">
                    <button
                      onClick={() => {
                        setCurrentTheme('light');
                        setShowThemeMenu(false);
                      }}
                      className={`w-full flex items-center space-x-2 px-3 py-2 text-sm hover:bg-gray-50 ${
                        currentTheme === 'light' ? 'text-orange-600 bg-orange-50' : 'text-gray-700'
                      }`}
                    >
                      <Sun className="h-4 w-4" />
                      <span>Light</span>
                    </button>
                    <button
                      onClick={() => {
                        setCurrentTheme('dark');
                        setShowThemeMenu(false);
                      }}
                      className={`w-full flex items-center space-x-2 px-3 py-2 text-sm hover:bg-gray-50 ${
                        currentTheme === 'dark' ? 'text-orange-600 bg-orange-50' : 'text-gray-700'
                      }`}
                    >
                      <Moon className="h-4 w-4" />
                      <span>Dark</span>
                    </button>
                  </div>
                )}
              </div>

              {/* Language Toggle */}
              <div className="relative" onClick={(e) => e.stopPropagation()}>
                <button
                  onClick={() => setShowLanguageMenu(!showLanguageMenu)}
                  className="bg-gray-100 hover:bg-gray-200 text-gray-700 px-3 py-2 rounded-xl text-sm font-medium transition-all duration-300 flex items-center space-x-1"
                >
                  <Globe className="h-4 w-4" />
                  <span className="uppercase font-bold">{currentLanguage}</span>
                  <ChevronDown className="h-3 w-3" />
                </button>

                {showLanguageMenu && (
                  <div className="absolute right-0 mt-2 w-40 bg-white rounded-xl shadow-lg border border-gray-200 py-2 z-50">
                    <button
                      onClick={() => {
                        setCurrentLanguage('hi');
                        setShowLanguageMenu(false);
                      }}
                      className={`w-full flex items-center space-x-3 px-3 py-2 text-sm hover:bg-gray-50 ${
                        currentLanguage === 'hi' ? 'text-orange-600 bg-orange-50' : 'text-gray-700'
                      }`}
                    >
                      <span className="text-lg">üáÆüá≥</span>
                      <span>‡§π‡§ø‡§Ç‡§¶‡•Ä</span>
                    </button>
                    <button
                      onClick={() => {
                        setCurrentLanguage('en');
                        setShowLanguageMenu(false);
                      }}
                      className={`w-full flex items-center space-x-3 px-3 py-2 text-sm hover:bg-gray-50 ${
                        currentLanguage === 'en' ? 'text-orange-600 bg-orange-50' : 'text-gray-700'
                      }`}
                    >
                      <span className="text-lg">üá∫üá∏</span>
                      <span>English</span>
                    </button>
                  </div>
                )}
              </div>
            </div>

            {/* User Authentication */}
            {user ? (
              <div className="relative" onClick={(e) => e.stopPropagation()}>
                <button
                  onClick={() => setShowProfileMenu(!showProfileMenu)}
                  className="flex items-center space-x-3 text-gray-700 hover:text-orange-600 px-3 py-2 rounded-lg text-sm font-medium transition-all duration-300 hover:bg-orange-50 group"
                >
                  {user.avatar ? (
                    <div className="relative">
                      <Image
                        src={user.avatar}
                        alt={user.name}
                        width={40}
                        height={40}
                        className="rounded-full ring-2 ring-gray-200 group-hover:ring-orange-300 transition-all duration-300"
                      />
                      <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white"></div>
                    </div>
                  ) : (
                    <div className="relative">
                      <div className={`w-10 h-10 rounded-full flex items-center justify-center text-white text-sm font-bold shadow-lg transition-all duration-300 group-hover:scale-110 ${
                        user.role === 'super_admin' 
                          ? 'bg-gradient-to-r from-purple-600 to-orange-600 ring-2 ring-purple-300' 
                          : user.role === 'admin' 
                          ? 'bg-gradient-to-r from-blue-600 to-orange-600 ring-2 ring-blue-300'
                          : 'bg-gradient-to-r from-orange-600 to-orange-700 ring-2 ring-orange-300'
                      }`}>
                        {user.name.charAt(0).toUpperCase()}
                      </div>
                      <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white animate-pulse"></div>
                      {user.role === 'super_admin' && (
                        <div className="absolute -top-1 -right-1 w-4 h-4 bg-yellow-500 rounded-full border-2 border-white flex items-center justify-center">
                          <span className="text-xs">üëë</span>
                        </div>
                      )}
                    </div>
                  )}
                  <div className="text-left">
                    <div className="font-semibold text-gray-900">{user.name}</div>
                    <div className={`text-xs font-medium ${
                      user.role === 'super_admin' 
                        ? 'text-purple-600' 
                        : user.role === 'admin' 
                        ? 'text-blue-600'
                        : 'text-gray-500'
                    }`}>
                      {user.role === 'super_admin' ? 'üëë Super Admin' : 
                       user.role === 'admin' ? 'üõ°Ô∏è Admin' : 'üë§ User'}
                    </div>
                  </div>
                  <div className="ml-2">
                    <svg className="w-4 h-4 transition-transform duration-300 group-hover:rotate-180" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                    </svg>
                  </div>
                </button>

                {/* Profile Dropdown */}
                {showProfileMenu && (
                  <div className="absolute right-0 mt-3 w-80 bg-white rounded-2xl shadow-2xl border border-gray-100 py-2 z-50 transform transition-all duration-300 ease-out">
                    {/* User Info Header */}
                    <div className="px-6 py-4 border-b border-gray-100 bg-gradient-to-r from-orange-50 to-orange-100 rounded-t-2xl">
                      <div className="flex items-center space-x-4">
                        {user.avatar ? (
                          <div className="relative">
                            <Image
                              src={user.avatar}
                              alt={user.name}
                              width={56}
                              height={56}
                              className="rounded-full ring-2 ring-orange-300"
                            />
                            <div className="absolute -bottom-1 -right-1 w-5 h-5 bg-green-500 rounded-full border-2 border-white"></div>
                          </div>
                        ) : (
                          <div className="relative">
                            <div className={`w-14 h-14 rounded-full flex items-center justify-center text-white font-bold shadow-lg ${
                              user.role === 'super_admin' 
                                ? 'bg-gradient-to-r from-purple-600 to-orange-600' 
                                : user.role === 'admin' 
                                ? 'bg-gradient-to-r from-blue-600 to-orange-600'
                                : 'bg-gradient-to-r from-orange-600 to-orange-700'
                            }`}>
                              {user.name.charAt(0).toUpperCase()}
                            </div>
                            <div className="absolute -bottom-1 -right-1 w-5 h-5 bg-green-500 rounded-full border-2 border-white animate-pulse"></div>
                            {user.role === 'super_admin' && (
                              <div className="absolute -top-1 -right-1 w-6 h-6 bg-yellow-500 rounded-full border-2 border-white flex items-center justify-center">
                                <span className="text-sm">üëë</span>
                              </div>
                            )}
                          </div>
                        )}
                        <div className="flex-1">
                          <h3 className="font-bold text-gray-900 text-lg">{user.name}</h3>
                          <p className="text-sm text-gray-600">{user.email}</p>
                          <span className={`inline-flex items-center px-3 py-1 text-xs font-semibold rounded-full mt-2 ${
                            user.role === 'super_admin' 
                              ? 'bg-purple-100 text-purple-800 border border-purple-200' 
                              : user.role === 'admin' 
                              ? 'bg-blue-100 text-blue-800 border border-blue-200'
                              : 'bg-gray-100 text-gray-800 border border-gray-200'
                          }`}>
                            {user.role === 'super_admin' ? 'üëë Super Admin' : 
                             user.role === 'admin' ? 'üõ°Ô∏è Admin' : 'üë§ User'}
                          </span>
                        </div>
                      </div>
                    </div>

                    {/* Menu Items */}
                    <div className="py-2">
                      <Link
                        href="/dashboard"
                        className="group flex items-center px-6 py-3 text-sm text-gray-700 hover:bg-orange-50 hover:text-orange-600 transition-all duration-200"
                        onClick={() => setShowProfileMenu(false)}
                      >
                        <div className="w-10 h-10 bg-gray-100 group-hover:bg-orange-100 rounded-xl flex items-center justify-center mr-4 transition-colors">
                          <User className="h-5 w-5" />
                        </div>
                        <div>
                          <p className="font-semibold">Profile</p>
                          <p className="text-xs text-gray-500">Manage your account</p>
                        </div>
                      </Link>

                      <Link
                        href="/dashboard/blogs"
                        className="group flex items-center px-6 py-3 text-sm text-gray-700 hover:bg-orange-50 hover:text-orange-600 transition-all duration-200"
                        onClick={() => setShowProfileMenu(false)}
                      >
                        <div className="w-10 h-10 bg-gray-100 group-hover:bg-orange-100 rounded-xl flex items-center justify-center mr-4 transition-colors">
                          <PenTool className="h-5 w-5" />
                        </div>
                        <div>
                          <p className="font-semibold">My Blogs</p>
                          <p className="text-xs text-gray-500">View and edit your posts</p>
                        </div>
                      </Link>

                      <Link
                        href="/write-blog"
                        className="group flex items-center px-6 py-3 text-sm text-gray-700 hover:bg-orange-50 hover:text-orange-600 transition-all duration-200"
                        onClick={() => setShowProfileMenu(false)}
                      >
                        <div className="w-10 h-10 bg-gray-100 group-hover:bg-orange-100 rounded-xl flex items-center justify-center mr-4 transition-colors">
                          <span className="text-lg">‚úçÔ∏è</span>
                        </div>
                        <div>
                          <p className="font-semibold">Write Blog</p>
                          <p className="text-xs text-gray-500">Create new content</p>
                        </div>
                      </Link>

                      {(user.role === 'admin' || user.role === 'super_admin') && (
                        <>
                          <div className="border-t border-gray-100 my-2"></div>
                          <Link
                            href="/admin/users"
                            className="group flex items-center px-6 py-3 text-sm text-gray-700 hover:bg-blue-50 hover:text-blue-600 transition-all duration-200"
                            onClick={() => setShowProfileMenu(false)}
                          >
                            <div className="w-10 h-10 bg-blue-100 group-hover:bg-blue-200 rounded-xl flex items-center justify-center mr-4 transition-colors">
                              <Users className="h-5 w-5" />
                            </div>
                            <div>
                              <p className="font-semibold">Manage Users</p>
                              <p className="text-xs text-gray-500">Admin panel</p>
                            </div>
                          </Link>
                        </>
                      )}

                      {user.role === 'super_admin' && (
                        <Link
                          href="/admin/settings"
                          className="group flex items-center px-6 py-3 text-sm text-gray-700 hover:bg-purple-50 hover:text-purple-600 transition-all duration-200"
                          onClick={() => setShowProfileMenu(false)}
                        >
                          <div className="w-10 h-10 bg-purple-100 group-hover:bg-purple-200 rounded-xl flex items-center justify-center mr-4 transition-colors">
                            <span className="text-lg">‚öôÔ∏è</span>
                          </div>
                          <div>
                            <p className="font-semibold">Site Settings</p>
                            <p className="text-xs text-gray-500">Super admin only</p>
                          </div>
                        </Link>
                      )}
                    </div>

                    {/* Logout Section */}
                    <div className="border-t border-gray-100 mt-2">
                      <button
                        onClick={() => {
                          handleLogout();
                          setShowProfileMenu(false);
                        }}
                        className="group w-full flex items-center px-6 py-3 text-sm text-red-600 hover:bg-red-50 transition-all duration-200 rounded-b-2xl"
                      >
                        <div className="w-10 h-10 bg-red-100 group-hover:bg-red-200 rounded-xl flex items-center justify-center mr-4 transition-colors">
                          <LogOut className="h-5 w-5" />
                        </div>
                        <div className="text-left">
                          <p className="font-semibold">Logout</p>
                          <p className="text-xs text-gray-500">Sign out of your account</p>
                        </div>
                      </button>
                    </div>
                  </div>
                )}
              </div>
            ) : (
              <div className="flex items-center space-x-4">
                <Link
                  href="/login"
                  className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-sm font-medium transition-colors"
                >
                  Login
                </Link>
                <Link
                  href="/register"
                  className="bg-orange-600 text-white hover:bg-orange-700 px-4 py-2 rounded-md text-sm font-medium transition-colors"
                >
                  Sign Up
                </Link>
              </div>
            )}
          </div>

          {/* Mobile menu button */}
          <div className="md:hidden">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="text-gray-700 hover:text-orange-600 focus:outline-none focus:text-orange-600"
            >
              {isOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
            </button>
          </div>
        </div>

        {/* Mobile Navigation */}
        {isOpen && (
          <div className="md:hidden border-t border-gray-200 py-4">
            <div className="flex flex-col space-y-4">
              <Link
                href="/"
                className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-base font-medium"
                onClick={() => setIsOpen(false)}
              >
                Home
              </Link>
              <Link
                href="/blogs"
                className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-base font-medium"
                onClick={() => setIsOpen(false)}
              >
                Blogs
              </Link>
              <Link
                href="/about"
                className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-base font-medium"
                onClick={() => setIsOpen(false)}
              >
                About Us
              </Link>

              {/* Action Buttons Mobile */}
              <div className="border-t border-gray-200 pt-4 pb-2 space-y-3">
                <Link
                  href="/donate"
                  className="flex items-center space-x-3 bg-gradient-to-r from-pink-500 to-red-500 text-white px-4 py-3 rounded-xl font-medium mx-3"
                  onClick={() => setIsOpen(false)}
                >
                  <Heart className="h-5 w-5" />
                  <span>Contribute to our work</span>
                </Link>

                <div className="px-3 space-y-2">
                  <div className="flex items-center justify-between">
                    <span className="text-gray-700 font-medium">Theme</span>
                    <div className="flex bg-gray-100 rounded-lg p-1">
                      <button
                        onClick={() => setCurrentTheme('light')}
                        className={`flex items-center space-x-1 px-3 py-1 rounded-md text-sm ${
                          currentTheme === 'light' ? 'bg-white shadow-sm text-orange-600' : 'text-gray-600'
                        }`}
                      >
                        <Sun className="h-4 w-4" />
                        <span>Light</span>
                      </button>
                      <button
                        onClick={() => setCurrentTheme('dark')}
                        className={`flex items-center space-x-1 px-3 py-1 rounded-md text-sm ${
                          currentTheme === 'dark' ? 'bg-white shadow-sm text-orange-600' : 'text-gray-600'
                        }`}
                      >
                        <Moon className="h-4 w-4" />
                        <span>Dark</span>
                      </button>
                    </div>
                  </div>

                  <div className="flex items-center justify-between">
                    <span className="text-gray-700 font-medium">Language</span>
                    <div className="flex bg-gray-100 rounded-lg p-1">
                      <button
                        onClick={() => setCurrentLanguage('hi')}
                        className={`flex items-center space-x-2 px-3 py-1 rounded-md text-sm ${
                          currentLanguage === 'hi' ? 'bg-white shadow-sm text-orange-600' : 'text-gray-600'
                        }`}
                      >
                        <span>üáÆüá≥</span>
                        <span>‡§π‡§ø‡§Ç‡§¶‡•Ä</span>
                      </button>
                      <button
                        onClick={() => setCurrentLanguage('en')}
                        className={`flex items-center space-x-2 px-3 py-1 rounded-md text-sm ${
                          currentLanguage === 'en' ? 'bg-white shadow-sm text-orange-600' : 'text-gray-600'
                        }`}
                      >
                        <span>üá∫üá∏</span>
                        <span>Eng</span>
                      </button>
                    </div>
                  </div>
                </div>
              </div>

              {/* Write Blog - Only show for authenticated users */}
              {user && (
                <Link
                  href="/write-blog"
                  className="bg-orange-600 text-white hover:bg-orange-700 px-3 py-2 rounded-md text-base font-medium flex items-center space-x-2 mx-3"
                  onClick={() => setIsOpen(false)}
                >
                  <PenTool className="h-4 w-4" />
                  <span>Write Blog</span>
                </Link>
              )}

              {user ? (
                <>
                  <div className="border-t border-gray-200 pt-4">
                    <div className="flex items-center px-3 py-4 bg-gradient-to-r from-orange-50 to-orange-100 rounded-lg mx-3 mb-4">
                      {user.avatar ? (
                        <div className="relative">
                          <Image
                            src={user.avatar}
                            alt={user.name}
                            width={48}
                            height={48}
                            className="rounded-full ring-2 ring-orange-300"
                          />
                          <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white"></div>
                        </div>
                      ) : (
                        <div className="relative">
                          <div className={`w-12 h-12 rounded-full flex items-center justify-center text-white font-bold shadow-lg ${
                            user.role === 'super_admin' 
                              ? 'bg-gradient-to-r from-purple-600 to-orange-600' 
                              : user.role === 'admin' 
                              ? 'bg-gradient-to-r from-blue-600 to-orange-600'
                              : 'bg-gradient-to-r from-orange-600 to-orange-700'
                          }`}>
                            {user.name.charAt(0).toUpperCase()}
                          </div>
                          <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white animate-pulse"></div>
                          {user.role === 'super_admin' && (
                            <div className="absolute -top-1 -right-1 w-5 h-5 bg-yellow-500 rounded-full border-2 border-white flex items-center justify-center">
                              <span className="text-xs">üëë</span>
                            </div>
                          )}
                        </div>
                      )}
                      <div className="ml-4 flex-1">
                        <p className="text-lg font-bold text-gray-900">{user.name}</p>
                        <p className="text-sm text-gray-600">{user.email}</p>
                        <span className={`inline-flex items-center px-2 py-1 text-xs font-medium rounded-full mt-1 ${
                          user.role === 'super_admin' 
                            ? 'bg-purple-100 text-purple-800 border border-purple-200' 
                            : user.role === 'admin' 
                            ? 'bg-blue-100 text-blue-800 border border-blue-200'
                            : 'bg-gray-100 text-gray-800 border border-gray-200'
                        }`}>
                          {user.role === 'super_admin' ? 'üëë Super Admin' : 
                           user.role === 'admin' ? 'üõ°Ô∏è Admin' : 'üë§ User'}
                        </span>
                      </div>
                    </div>
                  </div>

                  <Link
                    href="/dashboard"
                    className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-base font-medium"
                    onClick={() => setIsOpen(false)}
                  >
                    Profile
                  </Link>

                  <Link
                    href="/dashboard/blogs"
                    className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-base font-medium"
                    onClick={() => setIsOpen(false)}
                  >
                    My Blogs
                  </Link>

                  {(user.role === 'admin' || user.role === 'super_admin') && (
                    <Link
                      href="/admin/users"
                      className="text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-base font-medium"
                      onClick={() => setIsOpen(false)}
                    >
                      Manage Users
                    </Link>
                  )}

                  <button
                    onClick={() => {
                      handleLogout();
                      setIsOpen(false);
                    }}
                    className="text-left text-red-600 hover:text-red-700 px-3 py-2 rounded-md text-base font-medium w-full"
                  >
                    Logout
                  </button>
                </>
              ) : (
                <div className="border-t border-gray-200 pt-4 space-y-2">
                  <Link
                    href="/login"
                    className="block text-gray-700 hover:text-orange-600 px-3 py-2 rounded-md text-base font-medium"
                    onClick={() => setIsOpen(false)}
                  >
                    Login
                  </Link>
                  <Link
                    href="/register"
                    className="block bg-orange-600 text-white hover:bg-orange-700 px-3 py-2 rounded-md text-base font-medium text-center mx-3"
                    onClick={() => setIsOpen(false)}
                  >
                    Sign Up
                  </Link>
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </nav>
  );
} 